.row{
    color: white;
    margin-left: 10px;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}


.row__posters::-webkit-scrollbar {
    display: none;
}



.row_poster{
   
width: 100%;
object-fit: contain;
max-height: 100px;
margin-right: 10px;
transition: transform 450ms;

}

.row_poster:hover {
    transform: scale(1.08);
}

.row_posterLarge{
    max-height: 250px;
}

.row_posterLarge:hover {
    transform: scale(1.09);
    
}
